@import './../../../index';

.body {
  //background-color: #519a83;
  margin-bottom: 15px;
  background: $turquoiseColor;
}

.banner {
  position: relative;
  width: 100%;
  padding-bottom: 33%;

  img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

//.content {
//  display: flex;
//  align-items: start;
//  padding: 20px;
//}

.profile {
  padding: 10px;
  display: flex;
  //flex-wrap: wrap;
  column-gap: 20px;

}

.avatar {
  position: relative;
  flex: 0 1 150px;
}

.image {
  position: relative;
  width: 100%;
  padding-bottom: 100%;
  border-radius: 50%;
  overflow: hidden;
  border: 2px solid $lightTurquoiseColor;

  img {
    position: absolute;
    z-index: 5;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.input {
  width: 100%;
  padding: 5px;
  border-radius: 5px;
  background-color: #c8d6ca;
  border: 1px solid $brownColor;
}

.info {
  flex: 1 1 auto;
  letter-spacing: 1.1px;
}

.name {
  padding-top: 15px;
  //font-size: 24px;
  font-weight: 700;
  letter-spacing: 1.5px;
  margin-bottom: 25px;
  @include adaptiveValue("font-size", 24, 10, 1)
}

.button {
  background-color: $lightTurquoiseColor;
  color: $brownColor;
  font-weight: 600;
  border: 1px solid #5c3d46;
  padding: 10px 30px 10px 20px;
  font-size: 14px;
  align-self: start;
  border-radius: 5px;
  flex-grow: 0;
  flex-shrink: 0;
  flex-basis: 130px;
}
.buttonEdit{
  position: relative;
  transition: all 0.3s ease 0s;

  &:after{
    content: "";
    position: absolute;
    height: 18px;
    width: 18px;
    right: 5px;
    background: url(./../../../files/images/icons/edit-svgrepo-com.svg) center no-repeat;
    background-size: contain;
  }
    &:hover{
      &:after{
        transform: scale(1.2);
      }
    }
}
.buttonSave{
  padding: 10px 20px;
}

.custom {
  position: absolute;
  top: 10px;
  left: 10px;
  height: 15px;
  width: 15px;
  z-index: 10;
}

.customButtonAddPhoto {
  font-size: 24px;
  position: relative;
  cursor: pointer;
  transition: all 0.3s ease 0s;
  &::before {
    content: "";
    position: absolute;
    right: 0;
    height: 28px;
    width: 28px;
    background: url(./../../../files/images/icons/camera-add-photo-svgrepo-com.svg) center no-repeat;
    background-size: contain;
  }
  &:hover{
    &:before{
      transform: scale(1.1);
    }
  }
}
